@import "../../../css/module/color";
@import "../../../css/module/fn";

$imgWidth: 685;
$imgHeight: 237;

.imgBlock {
	width: p2m($imgWidth);
	height: p2m($imgHeight);
	border-radius: p2m(8);
	margin: 0 auto;
	border: p2m(8) solid white;
	background: {
		//color: #eee;
		size: 100% 100%;
	}
	
	a {
		display: block;
		line-height: p2m($imgHeight);
		width: 100%;
		height: 100%;
		text-align: center;
		font-size: p2m(36);
		font-weight: 900;
		color: #fff;
	}
}

#ulBuyerList, .ulBuyerList {
	width: p2m(700);
	height: p2m(235);
	border-radius: p2m(8);
	margin: 0 auto;
	
	> li {
		height: p2m(128);
		line-height: p2m(128);
		font-size: p2m(28);
		padding: 0 p2m(50);
		@include shadow();
		background-color: #fff;
		text-overflow:ellipsis;
		white-space:nowrap;
		overflow:hidden;

		> a {
			display: inline-block;
			min-width: p2m(500);
			height: 100%;
			
			&:active {
				color: $red;
			}
		}
	}
}

.ulBuyerList {
	height: auto;
}

#divBuyerList {
	
	> h1 {
		margin: 1.5em 1em;
		font-size: p2m(30);
		color: #000;
		text-transform: capitalize;
	}
}

#divBuyerThumbList , .divThumbView{
	width: p2m(700);
	border-radius: p2m(8);
	margin: 0 auto;
	padding: p2m(50) p2m(25);
	background-color: #fff;
	@include shadow();
	
	> h1 {
		font-size: p2m(30);
		color: #000;
		margin-bottom: 1.5em;
		text-transform: capitalize;
	}
	
	> ul {
		//display: flex;
		margin: 0 auto p2m(80);
		//width: p2m(650);
		
		> li {
			display: inline-block;
			background-repeat: no-repeat ;
			background-position: 2px 2px;
			vertical-align: top;
			width: p2m(300);
			height: p2m(300);
			@include shadow();
			@include textClip();
			text-align: center;
			color: #888;
			font-size: p2m(24);
			
			margin-bottom: p2m(30);
			padding: p2m(15);
			
			&:nth-of-type(2n+1) {
				margin-right: p2m(30);
			}
			
			> .imgHold {
				display: block;
				margin: p2m(20) auto 1em;
				width: p2m(155);
				height: p2m(155);
				background: {
					repeat: no-repeat;
					position: center center;
					size: 100% auto;
				}
			}
			
			> .icon-add {
				position: absolute;
    			width: 30px;
    			margin: 0 -70px;
			}

			> .icon-heart {
				position: absolute;
				margin-left: 40px;
				width: 30px;
			}
		}
	}
}

.detailContainer {
	
	margin-top: p2m(175);
	
	> .detail {
		background-color: #fff;
		border-radius: p2m(20);
		//height: p2m(870);
		//padding-bottom: p2m(20);
		width: p2m(590);
		text-align: center;
		@include shadow();
		
		> .headImg {
			//display: inline-block;
			line-height: p2m(420);
			display:  -webkit-inline-flex;
			-webkit-box-align: center;
			-webkit-align-items: center;
			-webkit-justify-content: center;
			display: inline-flex;
			align-items:center;
			justify-content: center;
			width: p2m(545);
			height: p2m(420);
			margin: p2m(-196) auto 0;
			border-radius: p2m(12);
			@include shadow(1, 20px);
			
			background: {
				color: #fff;
				repeat: no-repeat;
				position: center center;
				size: 100% auto;
			}
			
			>.headThumb{
				max-height: 100%;
				max-width: 100%;
				padding: p2m(80);
			}
		;
		}
		
		> .detailText {
			text-align: left;
			padding: p2m(25);
			max-height: p2m(500);
			overflow: auto;
			margin-bottom: p2m(30);
			
			.pTitle {
				color: #000;
				font-size: p2m(32);
			}
			.pMsg {
				//color: $red;
				word-break: break-all;
				font-size: p2m(32);
				margin: p2m(16) 0 p2m(10);
				text-indent: 1.52em;
				background: {
					image: url("/pages/msgBox/assets/ico32-book.png");
					repeat: no-repeat;
					position: left 3px;
					size: p2m(32) p2m(32);
				}
			}
			
			.pTxt {
				color: #555;
				font-size: p2m(32);
				text-indent: 1.52em;
				margin-bottom: p2m(20);
			}
		}
	}
	
}

